'use client'

import { useState, useEffect } from 'react'
import Image from 'next/image'
import Link from 'next/link'
import { ChevronLeft, ChevronRight } from 'lucide-react'
import bannerss from '@/datas/SubNavbar.json'
const banners = bannerss

export default function SubNavbar() {
	const [current, setCurrent] = useState(0)

	useEffect(() => {
		const timer = setInterval(() => {
			setCurrent(prev => (prev + 1) % banners.length)
		}, 5000)
		return () => clearInterval(timer)
	}, [])

	const goToPrevious = () => {
		setCurrent(prev => (prev - 1 + banners.length) % banners.length)
	}

	const goToNext = () => {
		setCurrent(prev => (prev + 1) % banners.length)
	}

	return (
		<div className='w-full bg-gray-50 py-6'>
			<div className='container mx-auto px-4'>
				<div className='relative w-full h-72 rounded-2xl overflow-hidden group'>
					<Link href={banners[current].urlLink}>
						<Image
							src={banners[current].imageUrl || '/placeholder.svg'}
							alt='Banner'
							fill
							className='object-cover'
							priority
						/>
					</Link>

					{/* Previous Button */}
					<button
						onClick={goToPrevious}
						className='absolute left-4 top-1/2 -translate-y-1/2 z-10 bg-white/80 hover:bg-white p-2 rounded-full hidden group-hover:flex transition'
					>
						<ChevronLeft className='h-6 w-6 text-gray-800' />
					</button>

					{/* Next Button */}
					<button
						onClick={goToNext}
						className='absolute right-4 top-1/2 -translate-y-1/2 z-10 bg-white/80 hover:bg-white p-2 rounded-full hidden group-hover:flex transition'
					>
						<ChevronRight className='h-6 w-6 text-gray-800' />
					</button>

					{/* Indicators */}
					<div className='absolute bottom-4 left-1/2 -translate-x-1/2 flex gap-2 z-10'>
						{banners.map((_, idx) => (
							<button
								key={idx}
								onClick={() => setCurrent(idx)}
								className={`w-2 h-2 rounded-full transition ${
									idx === current ? 'bg-white' : 'bg-white/50'
								}`}
							/>
						))}
					</div>
				</div>
			</div>
		</div>
	)
}
