'use client'

import Image from 'next/image'
import Link from 'next/link'
import { Star, ShoppingCart, Heart, Minus, Plus } from 'lucide-react'
import { useCart } from '@/context/CartContext'
import productss from '@/datas/Products.json'

const products = productss

export default function Products() {
	const { getItemQuantity, increaseCartQuantity, decreaseCartQuantity } =
		useCart()

	const formatPrice = price => {
		return new Intl.NumberFormat('uz-UZ').format(price)
	}

	return (
		<section className='py-16 bg-white'>
			<div className='container mx-auto px-4'>
				<h2 className='text-3xl font-bold text-gray-900 mb-10'>
					Ommabop Mahsulotlar
				</h2>

				<div className='grid grid-cols-2 md:grid-cols-3 lg:grid-cols-6 gap-3 md:gap-4'>
					{products.map(product => {
						const quantity = getItemQuantity(product.id)

						return (
							<div
								key={product.id}
								className='group bg-white rounded-xl border border-gray-100 hover:border-gray-300 transition overflow-hidden flex flex-col'
							>
								{/* Image Section */}
								<div className='relative w-full aspect-square bg-gray-50 overflow-hidden'>
									{product.discount > 0 && (
										<div className='absolute top-2 left-2 z-10 bg-orange-500 text-white text-[10px] font-bold px-2 py-1 rounded'>
											-{product.discount}%
										</div>
									)}
									<button className='absolute top-2 right-2 z-10 text-gray-400 hover:text-red-500 transition'>
										<Heart className='w-4 h-4' />
									</button>

									<Link href={`/product/${product.id}`}>
										<Image
											src={product.imageUrl || '/placeholder.svg'}
											alt={product.nomi}
											fill
											className='object-contain p-3 group-hover:scale-105 transition'
										/>
									</Link>
								</div>

								{/* Content */}
								<div className='p-3 flex flex-col flex-grow gap-2'>
									<Link href={`/product/${product.id}`}>
										<h3 className='text-xs md:text-sm text-gray-800 line-clamp-2 hover:text-blue-600 transition font-medium'>
											{product.nomi}
										</h3>
									</Link>

									<div className='flex items-center gap-1'>
										<Star className='w-3 h-3 text-yellow-400 fill-yellow-400' />
										<span className='text-xs text-gray-600 font-semibold'>
											{product.rating}
										</span>
										<span className='text-xs text-gray-400'>
											({product.reviewsCount})
										</span>
									</div>

									<div className='mt-auto'>
										{product.oldPrice && (
											<div className='text-xs text-gray-400 line-through'>
												{formatPrice(product.oldPrice)} so'm
											</div>
										)}
										<div className='text-sm font-bold text-blue-600'>
											{formatPrice(product.newPrice)} so'm
										</div>
									</div>
								</div>

								{/* Cart Button */}
								<div className='p-3 pt-0 border-t border-gray-100'>
									{quantity === 0 ? (
										<button
											onClick={() => increaseCartQuantity(product.id)}
											className='w-full flex items-center justify-center gap-2 px-3 py-2 text-xs font-semibold text-orange-600 border border-orange-500 rounded-lg hover:bg-orange-50 transition'
										>
											<ShoppingCart className='w-3.5 h-3.5' />
											<span className='hidden sm:inline'>Savat</span>
										</button>
									) : (
										<div className='flex items-center justify-between border border-orange-500 rounded-lg overflow-hidden'>
											<button
												onClick={() => decreaseCartQuantity(product.id)}
												className='flex-1 h-8 flex items-center justify-center hover:bg-orange-50 transition'
											>
												<Minus className='w-3.5 h-3.5 text-orange-600' />
											</button>
											<span className='flex-1 text-center text-xs font-bold text-gray-800'>
												{quantity}
											</span>
											<button
												onClick={() => increaseCartQuantity(product.id)}
												className='flex-1 h-8 flex items-center justify-center hover:bg-orange-50 transition'
											>
												<Plus className='w-3.5 h-3.5 text-orange-600' />
											</button>
										</div>
									)}
								</div>
							</div>
						)
					})}
				</div>
			</div>
		</section>
	)
}
